import userImg from './image/user.png';
import help from './image/help.png';
import logout from './image/logout.png';
import profile from './image/profile.png';
import setting from './image/setting.png';
import React, { useEffect, useState, useRef } from 'react';
import './App.css';
import { Login } from './Login';
import { Register } from "./Register";
import { Test } from './components/Test';
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import secondpage from './pages/secondpage'
import Home from './pages/Home';

function App() {
  const [open, setOpen] = useState(false); //this is for dropdown
  const [isToggled, setIsToggled] = useState({
    toggle_Wing: false, //this is for wing button
    toggle_countWing: false, //this is for count of wing button
    toggle_floor: false, //this is for no of floor button
    toggle_ishundred: false, //this is for ishundred button
    toggle_flats: false //this is for flats button
  });

  let menuRef = useRef();

  useEffect(() => {
    let handler = (e) => {
      if (!menuRef.current.contains(e.target)) {
        setOpen(false);
        console.log(menuRef.current);
      }
    };

    document.addEventListener("mousedown", handler);

    return () => {
      document.removeEventListener("mousedown", handler);
    }
  });

  const [currentForm, setCurrentForm] = useState('Login');

  const toggleForm = (formName) => {
    setCurrentForm(formName);
  }

  return (
    <div className="App">
      <button onClick={() => setIsToggled(!isToggled)}>Wing</button>
      {isToggled && <test />}
      {isToggled ? <Test /> : <p>isOn</p>}

      <button onClick={() => setIsToggled(!isToggled)}>Count of wing</button>
      {isToggled && <test />}
      {isToggled ? <Test /> : <p>isOn</p>}

      <button onClick={() => setIsToggled(!isToggled)}>No of floor</button>
      {isToggled && <test />}
      {isToggled ? <Test /> : <p>isOn</p>}

      <button onClick={() => setIsToggled(!isToggled)}>ishundred</button>
      {isToggled && <test />}
      {isToggled ? <Test /> : <p>isOn</p>}

      <button onClick={() => setIsToggled(!isToggled)}>No of flats per floor</button>
      {isToggled && <test />}
      {isToggled ? <Test /> : <p>isOn</p>}

      <BrowserRouter>
        <Routes>
          <Route index element={<Home />} Route />
          <Route path="/home" element={<Home />} />
          <Route path="/secondpage" element={<secondpage />} />
        </Routes>
      </BrowserRouter>

      {
        currentForm === "Login" ? <Login onFormSwitch={toggleForm} /> : <Register onFormSwitch={toggleForm} />
      }
      <div className='menu-container' ref={menuRef}>
        <div className='menu-trigger' onClick={() => { setOpen(!open) }}>
          <img src={userImg}></img>
        </div>
        <div className={`dropdown-menu ${open ? 'active' : 'inactive'}`} >
          <h3>The Kiet<br /><span>Website Designer</span></h3>
          <ul>
            <DropdownItem img={profile} text={"Profile"} />
            <DropdownItem img={setting} text={"Setting"} />
            <DropdownItem img={help} text={"Help"} />
            <DropdownItem img={logout} text={"Logout"} />
          </ul>
        </div>
      </div>
    </div>
  );
}

function DropdownItem(props) {
  return (
    <li className='dropdownItem'>
      <img src={props.img}></img>
      <a> {props.text} </a>
    </li>
  );
}

export default App;